import React, { Component } from 'react';
import ame from './img/card/あめ.png';
import inu from './img/card/いぬ.png';
import ugai from './img/card/うがい.png';
import usi from './img/card/うし.png';
import uma from './img/card/うま.png';
import umi from './img/card/うみ.png';
import otera from './img/card/おてらのかね.png';
import onara from './img/card/おなら.png';
import oniku from './img/card/おにく.png';
import kaeru from './img/card/かえる.png';
import karasu from './img/card/カラス.png';
import gitaa from './img/card/ギター.png';
import kyuukyuusya from './img/card/きゅうきゅうしゃ.png';
import kyoukai from './img/card/きょうかいのかね.png';
import kutibue from './img/card/くちぶえ.png';
import gutugutu from './img/card/ぐつぐつにる.png';
import kurakusyon from './img/card/くるま.png';
import chaimu from './img/card/げんかんのチャイム.png';
import doriru from './img/card/こうじのドリル.png';
import koorogi from './img/card/コオロギ.png';
import syoubousya from './img/card/しょうぼうしゃ.png';
import sinzou from './img/card/しんぞうのおと.png';
import sinbaru from './img/card/シンバル.png';
import suzume from './img/card/すずめ.png';
import tukutukuhousi from './img/card/つくつくぼうし.png';
import densya from './img/card/でんしゃ.png';
import denwa from './img/card/でんわ.png';
import tokei from './img/card/とけい.png';
import tori from './img/card/とりのはばたき.png';
import niwatori from './img/card/にわとり.png';
import neko from './img/card/ねこ.png';
import nomimono from './img/card/のみものをのむ.png';
import hakusyu from './img/card/はくしゅ.png';
import hasami from './img/card/はさみ.png';
import hanabi from './img/card/はなび.png';
import piano from './img/card/ピアノ.png';
import huurin from './img/card/ふうりん.png';
import humikiri from './img/card/ふみきり.png';
import hoissuru from './img/card/ホイッスル.png';
import mazikkupen from './img/card/マーカーでえをかく.png';
import mizu from './img/card/みずをそそぐ.png';
import mezamasi from './img/card/めざましどけい.png';
import yakan from './img/card/やかんがなる.png';
import yasai from './img/card/やさいをきる.png';
import raamen from './img/card/ラーメンをすする.png';
import close from './img/btn13.png';
import './view.css';

const imgList = {
    ame,inu,ugai,usi,uma,umi,otera,onara,oniku,kaeru,
    karasu,gitaa,kyuukyuusya,kyoukai,kutibue,gutugutu,
    kurakusyon,chaimu,doriru,koorogi,syoubousya,sinzou,sinbaru,
    suzume,tukutukuhousi,densya,denwa,tokei,tori,niwatori,
    neko,nomimono,hakusyu,hasami,hanabi,piano,huurin,humikiri,
    hoissuru,mazikkupen,mizu,mezamasi,yakan,yasai,raamen
}

class ViewAnswer extends Component {
    render() {
        return (
            <div>
                <div id="overlay">
                    {/* <div onClick={this.props.onClickModal} id="overlay"></div> */}
                </div>
                <div className="answerimage" id="ans">
                    {/* <div onClick={this.props.onClickCard}> */}
                        <img src={imgList[this.props.current]} id={this.props.current}></img>
                    {/* </div> */}
                    <div className="buttons">
                        <div onClick={this.props.onClickClose}>
                            <img src={close} id="close"></img>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default ViewAnswer;



